img {
  height: 40px;
  width: 40px;
  align-items: center;
  margin: 0px 30px;
  border-radius: 20%;
}

.container {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 80vw;
  margin-top: 10px;
  background: #6ac4eb;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to bottom, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
box-shadow: 0px 0px 8px 2px  #0F2027;
border-radius: 25px;
  padding-bottom: 30px;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 9px;
  width: 100%;
  margin-top: 30px;
}

.text {
  color: rgb(6, 6, 6);
  font-size: 48px;
  font-weight: 700;
}

.underline {
  width: 105px;
  height: 6px;
  background: rgb(10, 10, 10);
  border-radius: 9px;
}

.inputs {
  margin-top: 55px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.input  {
  display: flex;
  align-items: center;
  margin: auto;
  width: 50%;
  height: 80px;
  background: white;
  border-radius: 6px;
}

.input input {
    height: 50px;
    width: 50%;
    background: transparent;
    border: none;
    outline: none;
    color: grey;
    font-size: 20px;
}

.forgot-password {
    padding-left: 62px;
    margin-top: 27px;
    color: grey;
    font-size: 18px;
    margin-left: 20%;
}

.forgot-password span {
    color: red;
    cursor: pointer;
}
.submitc {
    display: flex;
    gap: 30px;
    margin: 60px auto;
}

.submit {
  display: flex;  
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 59px;
  color: white;
  background: rgb(235, 27, 27);
  border-radius: 50px;
  font-size: 19px;
  font-weight: 500;
  cursor: pointer;
}

.gray {
    background: greenyellow;
    color: black;
}